.header {
    & h2 {
        background-color: $primary;
        font-size: 16px;
        color: $white;
        font-weight: 400;
        text-align: center;
        padding-top: 8px;
        padding-bottom: 8px;
    }
}

.footer {
    background-color: $primary;
    padding-top: 80px;
    padding-bottom: 80px;
    color: #fff;
    text-align: center;

    & p {
        font-size: 20px;
    }
}

.socialmedia-list {
    display: flex;
    justify-content: center;
    gap: 24px;
    margin-bottom: 16px;

    & img {
        height: 24px;
        width: 24px;
    }
}